* {margin:0; box-sizing:border-box;}
button, a, li {cursor:pointer;}
ul {list-style:none;}
a {text-decoration:none; color:#333;}
h1, h2 {line-height:1.3; margin:15px 0;}
button {transition:all 0.5s ease-in-out; font-size:1em; padding:1em; width:100%; border-radius:5px; margin-top:10px;}
.button:hover {transform:scale(0.98);}
.hero-section {height:100vh; padding-top:15rem; background-color:transparent; z-index:-1;}
.hero-section .flex-hero {flex-direction:column; gap:30px; text-align:center;}
.hero-section .flex-hero h1 {font-size:65px; font-weight:900;}
.text-gradient {background:linear-gradient(45deg,#2190FF 2.34%,#b673f8 100.78%); -webkit-background-clip:text; background-clip:text; -webkit-text-fill-color:transparent;}
.hero-section h4 {font-size:22px; font-weight:300; text-align:center; padding:12px 14rem; color:#969BA5;}
.hero-section .button {padding:0.7rem 2rem; border-radius:25px; background-color:#2190FF; color:#fff; border:1px solid transparent; width:15%; margin-top:15px;}
.hero-section div:nth-child(2) {margin-top:4rem;}
.hero-section .image {flex-direction:column; justify-content:center; align-items:center; gap:0px;}


.flex-hero .blob-cont {justify-content:center; align-items:center; position:static; margin:10px;}
.blob {border-radius:100px; filter:blur(60px);}
.blue {background-color:#2937a392; position:absolute; top:0; left:0; right:55%; bottom:25%; margin:auto; height:150px; width:200px; animation:blobani 8s infinite ease;}
.blue1 {background-color:#2937a392; position:absolute; top:0; left:0; right:-55%; bottom:-25%; margin:auto; height:150px; width:200px; animation:blobani 8s infinite ease;}

@keyframes blobani {
0% {top:0; left:0; transform:scale(1);}
30% {top:0; left:0; transform:scale(1.2);}
60% {top:0; left:0; transform:scale(1.3);}
100% {top:0; left:0; transform:scale(1);}
}

.services-section {height:100vh; padding:150px 50px 25px; background-color:#f9fafc; text-align:center;}
.section-title {font-size:2.4rem; font-weight:600; color:#222; margin-bottom:15px;}
.section-subtitle {font-size:1.2rem; font-weight:400; color:#222; margin-bottom:40px;}
.services-container {display:grid; grid-template-columns:repeat(auto-fit, minmax(220px, 1fr)); gap:30px; justify-items:center;}
.service-card {background:#fff; border-radius:15px; padding:25px; box-shadow:0 5px 20px rgba(0, 0, 0, 0.08); transition:all 0.3s ease; width:100%; cursor: pointer;}
.service-card:hover {transform:translateY(-8px); box-shadow:0 8px 25px rgba(0, 0, 0, 0.15);}
.service-card img {width:auto; height:auto; object-fit:contain; margin-bottom:15px;}
.service-card h3 {font-size:1.1rem; font-weight:500; color:#333;}


@media screen and (max-width:980px) {
.hero-section h1 {text-align:center;}
.hero-section {padding-top:12rem;}
.hero-section h4 {font-size:21px; text-align:center; padding:0px 4rem;}
.image {width:500px;}
.hero-section .button {margin-top:15px; width:45%;}
}

@media screen and (max-width:400px) {
.hero-section h1 {font-size:35px;}
.hero-section {padding-top:10rem;}
.hero-section h4 {font-size:16px; padding:0px 2rem;}
}